<template>
  <el-dialog ref='term-delete' title="" :visible.sync='visible' size="tiny">
    <el-form   :model="annotation">
      <el-form-item>
        <div>{{'"'+annotation.term+'"'}}{{'"'+annotation.type+'"'}}?</div>
      </el-form-item>
      <el-form-item >
        <el-checkbox v-model="addChecked"></el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button type="danger" @click="mySubmit"></el-button>
        <el-button @click='visible=false'></el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>
<script>
    export default {
      props: ['annotation', 'submit'],
      data () {
        return {
          visible: false,
          addChecked:false,
          excludeChecked:false
        };
      },
      methods: {
        open () {
          this.visible = true
        },
        mySubmit: function(){
          this.visible = false
          this.submit()
        }
      }
    }
</script>


