import {Message,MessageBox} from 'element-ui'
const service = axios.create({
	baseURL:window.location.protocol+'//'+document.domain+':8080',//121.196.212.109:8080---annotation.malgo.cn
	// baseURL: 'http://test.annotation.malgo.cn',
	timeout:6000*10*60*24*2
})
service.interceptors.request.use(config=>{
	//
	// console.log(config)
	config.withCredentials=true// indicates whether or not cross-site Access-Control requests
	return config
  },
  error =>{
  	console.log(error)
  	Promise.reject(error)
})
service.interceptors.response.use(response=>{
	const res=response.data;
	if(res.is_succ===false){
	   MessageBox.confirm('', '', {
             confirmButtonText: '',
             cancelButtonText: '',
             type: 'info'
	      })
		  .then(()=>{//user/actions/
	  		sessionStorage.removeItem('user');
	  		location.reload();
	  		// this.$router.push({ path: '/login' });
		  })
	  return Promise.reject('error');
	}else{
		//
		return res;
	}
  },
  error=>{
  	console.log('err'+error)
    Message.error(error.message);
  	return Promise.reject(error)
  }
)
export default service


