import Router from 'vue-router'
import Login from '@/components/Login'
import TabPage from '@/components/TabPage'
import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import BrowseData from '@/components/BrowseData'
import QueryType from '@/components/QueryType'
import UnRecognizePage from '@/components/UnRecognizePage'
import AnnoQueryPage from '@/components/AnnoQueryPage'
import TypeManagePage from '@/components/TypeManagePage'
import KnowledgePage from '@/components/KnowledgePage'
import ModalTerm from '@/components/ModalTerm'
import TermPage from '@/components/TermPage'
import AtomicTermPage from '@/components/AtomicTermPage'
import ConceptPage from '@/components/ConceptPage'
import DrawPage from '@/components/DrawPage'
import ConceptGroup from '@/components/ConceptGroup'
export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'',
      component:Container,
      leaf:true,
      children:[
        {path:'/uiKnowledgePage',component:KnowledgePage,name:''},
      ]
    },
    {
      path:'/',
      name:'',
      component:Container,
      children:[
        {path:'/uiAnnoPage',component:AnnoPage,name:''},
        {path:'/uiUnRecognizePage',component:UnRecognizePage,name:''},
        {path:'/uiAnnoQueryPage',component:AnnoQueryPage,name:''},
        {path:'/uiDrawPage',component:DrawPage,name:''}
      ]
    },
    {
      path:'/',
      name:'',
      component:Container,
      children:[
        {path:'/uiTypeManagePage',component:TypeManagePage,name:''},
        // {path:'/browseData',component:BrowseData,name:''},
        // {path:'/queryType',component:QueryType,name:''},
        {path:'/uiTermPage',component:TermPage,name:''},
        {path:'/uiAtomicTermPage',component:AtomicTermPage,name:''},
        {path:'/uiConceptPage',component:ConceptPage,name:''},
        {path:'/uiConceptGroup',name:'',component:ConceptGroup,hidden:true}
      ]
    },

    {
      path:'/uiLogin',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path:'/',
      name:'excel',
      component:Container,
      leaf:true,
      children:[
        {path:'/uiExcel',component:ModalTerm,name:'excel'},
      ]
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/uiLogin'}
    }
  ]
})



